import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import styles from './_now-checkbox.scss';
import '@servicenow/now-icon';
import focusBehavior from '@servicenow/behavior-focus';
import {filterAriaAttributes} from '@servicenow/library-enhanced-utils';
import {t} from 'sn-translate';
import {configAriaSchema} from '@servicenow/library-schemas';

const handleChecked = (e, dispatch, interactive) => {
	if (!interactive) {
		return;
	}
	if (e.type === 'click' || (e.type === 'keydown' && e.key === ' ')) {
		e.preventDefault();
		dispatch(({properties: {name, checked}}) => {
			return {
				type: 'NOW_CHECKBOX#CHECKED_SET',
				payload: {
					name: name,
					value: checked ? false : true
				}
			};
		});
	}
};

const handleBlur = (dispatch) => {
	dispatch(({properties: {required, checked, name}}) => {
		return {
			type: 'NOW_CHECKBOX#INVALID_SET',
			payload: {name, value: required && !checked}
		};
	});
};

const view = (state, {dispatch}) => {
	const {
		checked,
		disabled,
		required,
		readonly,
		invalid,
		label,
		configAria
	} = state.properties;

	const interactive = !disabled && !readonly;
	const indeterminate = checked === 'indeterminate';

	return (
		<div
			class={{
				'now-checkbox': true,
				'is-disabled': disabled,
				'is-readonly': readonly,
				'is-invalid': invalid
			}}
			role="checkbox"
			tabindex={interactive ? 0 : -1}
			{...filterAriaAttributes(configAria, 'checkbox')}
			aria-checked={indeterminate ? 'mixed' : `${checked}`}
			aria-invalid={invalid ? 'true' : 'false'}
			aria-disabled={interactive ? 'false' : 'true'}
			on-click={(e) => handleChecked(e, dispatch, interactive)}
			on-keydown={(e) => handleChecked(e, dispatch, interactive)}
			on-blur={() => handleBlur(dispatch)}>
			<div className="now-checkbox-input">
				{checked ? (
					<now-icon
						className="now-checkbox-icon"
						icon={
							checked === 'indeterminate' ? 'minus-outline' : 'check-outline'
						}
						size="sm"
					/>
				) : (
					undefined
				)}
			</div>
			<span className="now-checkbox-label">
				<span className="now-line-height-crop">{label}</span>
			</span>
			{required ? (
				<now-icon
					className="now-checkbox-required-indicator"
					aria-label={t('required')}
					icon="asterisk-fill"
					size="sm"
				/>
			) : (
				undefined
			)}
		</div>
	);
};

/**
 * Input for users to communicate a yes, no, or indeterminate choice.
 * A checkbox can appear standalone or in a set of other options.
 *
 *
 * ```jsx
 * <now-checkbox label="I accept these terms" required />
 * ```
 *
 * @seismicElement now-checkbox
 * @summary An interactive control for indicating an affirmative, negative, or indeterminate choice.
 * @uib.label Checkbox
 * @uib.icon checkbox
 * @uib.description An interactive control for indicating an affirmative, negative, or indeterminate choice.
 * @uib.category primitives
 * @uib.associatedTypes global.core
 * @uib.properties label, checked, disabled, invalid, readonly, required, name, configAria
 * @uib.actions NOW_CHECKBOX#CHECKED_SET
 */
createEnhancedElement('now-checkbox', {
	properties: {
		/**
		 * The state of the checkbox. Options include:
		 * - `true`: The checkbox is selected.
		 * - `false`: The checkbox is cleared.
		 * - `indeterminate`: The checkbox appears with a horizontal line
		 *   inside to express that it is neither `true` nor `false`.
		 *   You can use the indeterminate state
		 *   when a single checkbox controls multiple child
		 *   elements. For example, a "select all" checkbox.
		 *
		 * When the user selects a checkbox in the `indeterminate` state, the
		 * property value changes to `false`.
		 * @type {(boolean|'indeterminate')}
		 * @uib.description True if the checkbox is selected; otherwise false. If the `checked` status is neither true nor false, "indeterminate" causes the checkbox to appear with a horizontal line inside of it.
		 * @uib.fieldType choice
		 * @uib.typeMetadata.choices [{"label": "True","value": true}, {"label": "False","value": false}, {"label": "Indeterminate","value": "indeterminate"}]
		 * @uib.defaultValue false
		 */
		checked: {
			default: false,
			manageable: true,
			schema: {oneOf: [{type: 'boolean'}, {const: 'indeterminate'}]}
		},
		/**
		 * An object whose keys reference a specific HTML element within the
		 * now-checkbox. The value of each key is an object that contains an ARIA
		 * property (or properties) and corresponding value/s. These ARIA
		 * properties will be set on the specified element.
		 * The 'checkbox' key corresponds to the `<div>` with an ARIA role of
		 * 'checkbox' in now-checkbox. See https://www.w3.org/TR/wai-aria-1.1/#checkbox for
		 * properties and accepted values.
		 * @type {{ 'aria-*': string }}
		 * @uib.label ARIA properties
		 * @uib.defaultValue {}
		 * @uib.description Configures ARIA properties
		 */
		configAria: {
			schema: {
				oneOf: [
					{
						type: 'object',
						properties: {
							checkbox: configAriaSchema
						},
						additionalProperties: false
					},
					configAriaSchema
				]
			}
		},
		/**
		 * When true, the user cannot select the checkbox.
		 * @type {boolean}
		 */
		disabled: {default: false, schema: {type: 'boolean'}},
		/**
		 * When true, the current state of the checkbox is invalid.
		 * The visual style changes accordingly.
		 * @type {boolean}
		 * @uib.description When true, the current state of the checkbox is invalid. An error displays and the visual style changes accordingly.
		 */
		invalid: {default: false, manageable: true, schema: {type: 'boolean'}},
		/**
		 * When true, the user cannot select the checkbox.
		 * @type {boolean}
		 * @uib.label Read Only
		 * @uib.description When true, the user cannot select the checkbox.
		 */
		readonly: {default: false, schema: {type: 'boolean'}},
		/**
		 * When true, the user must select the checkbox before they can
		 * submit the form. For example, a checkbox that the user must select
		 * to accept terms and conditions.
		 * @type {boolean}
		 * @uib.description When true, the user must select the checkbox before they can submit the form.
		 */
		required: {default: false, schema: {type: 'boolean'}},
		/**
		 * Text label to display next to the checkbox.
		 * @type {string}
		 * @uib.description Text label to display next to the checkbox.
		 * @uib.defaultValue Make a choice
		 * @uib.translatable true
		 */
		label: {schema: {type: 'string'}},
		/**
		 * Name of the checkbox. Dispatched along with the field value
		 * on user interaction.
		 * @type {string}
		 * @uib.label Identifier
		 * @uib.description Name of the checkbox. Dispatched along with the field value on user interaction
		 */
		name: {schema: {type: 'string'}},
		/**
		 * String value of the `checked` property.
		 * @type {('true'|'false'|'indeterminate')}
		 */
		value: {
			reflect: true,
			computed({properties: {checked}}) {
				return `${checked}`;
			},
			schema: {type: 'string'}
		}
	},
	dispatches: {
		/**
		 * Dispatched when the user interacts with the checkbox and neither
		 * `disabled` nor `readonly` are true. Set the `manageChecked` property
		 * to handle this event manually.
		 * @type {{name: string, value: boolean}}
		 * @uib.description Dispatched when the user interacts with the checkbox and neither `disabled` nor `readonly` are true
		 */
		'NOW_CHECKBOX#CHECKED_SET': {
			schema: {
				type: 'object',
				properties: {
					name: {type: 'string'},
					value: {type: 'boolean'}
				}
			}
		}
	},
	behaviors: [
		{
			behavior: focusBehavior
		}
	],
	styles,
	view
});
